.IntrinsicImage__wrapper {
  // display: flex;
}

.IntrinsicImage__inner {
  // flex: 1 1 auto;
  overflow: hidden;
  position: relative;
  width: 100vw;
  height: 66.67vw;
  top: 0;
  left: 0;
  z-index: -10;
}

.IntrinsicImage__main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.IntrinsicImage__main--loaded {
  margin-top: 0;
}

.IntrinsicImage__pre {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  filter: blur(5px);
  transform: scale(1.05);
  opacity: 1;
  transition: opacity 300ms ease-out;
}

.IntrinsicImage__pre--loaded {
  opacity: 0;
}
