// color defination ##
$colours: rgb(97, 236, 247), rgb(117, 192, 211), rgb(5, 203, 238),
  rgb(2, 132, 167), rgb(2, 103, 130), rgb(169, 167, 206), rgb(142, 117, 145),
  rgb(102, 77, 105), rgb(225, 225, 225), rgb(100, 100, 100), rgb(122, 122, 122),
  rgb(255, 210, 122), rgb(229, 77, 70);
$bkg_color: rgb(225, 225, 225);
$head_color: rgb(5, 203, 238);
$head_font: rgb(227, 230, 237);
$btn_bkg: rgb(5, 203, 238);
$btn_color: rgb(5, 203, 238);
$bkg_black: rgb(37, 43, 52);
$font: rgb(100, 100, 100);
$overlay_trans: rgba(37, 43, 52, 0.5);
$font_grey: rgb(71, 78, 87);
$font_white: rgb(253, 254, 252);

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  letter-spacing: 2px;
  line-height: 1.8;
  width: 100%;
  min-height: 1000px;
  background: $bkg_black;
  color: $font_white;
  overflow-x: hidden;
  overflow-y: scroll;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  #root {
    overscroll-behavior-y: none;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
